.login-box {
  /* position: absolute; */
  /* top: 50%; */
  /* left: 50%; */
  /* width: 400px; */
  padding-top: 20px;
  /* transform: translate(-50%, -50%); */
  /* background: rgba(24, 20, 20, 0.3); */
  box-sizing: border-box;
  /* box-shadow: 0 15px 25px rgba(0, 0, 0, 0.1); */
  border-radius: 10px;
}

.login-box .user-box {
  position: relative;
}

.login-box .user-box input {
  width: 100%;
  padding: 10px 0;
  font-size: 16px;
  color: rgb(255, 255, 255, 0.5);

  border: none;
  border-bottom: 1px solid #fff;
  outline: none;
  background: transparent;

  margin-bottom: 20px;
}

.login-box .user-box label {
  position: absolute;
  top: 0;
  left: 0;
  padding: 10px 0;
  font-size: 16px;
  color: #fff;
  pointer-events: none;
  transition: 0.5s;
}

.login-box .user-box input:focus ~ label,
.login-box .user-box input:valid ~ label {
  top: -20px;
  left: 0;
  color: #bdb8b8;
  font-size: 12px;
}

.login-box form a {
  position: relative;
  display: inline-block;
  padding: 10px 20px;
  color: #ffffff;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  overflow: hidden;
  transition: 0.5s;
  margin-top: 5px;
  letter-spacing: 4px;
}

